    if (that.type(arr) === 'object' && !key) {
      return clone;
    } else if(typeof arr !== 'object') { //  arr 
      return [clone];
    }

    // 
    clone.sort(function(o1, o2) {
      var v1 = o1[key];
      var v2 = o2[key];

      /*
       * 
       * 
       */

      // 
      if (!isNaN(o1) && !isNaN(o2)) return o1 - o2;

      // 
      if (!isNaN(o1) && isNaN(o2)) {
        if(key && typeof o2 === 'object'){
          v1 = o1;
        } else {
          return -1;
        }
      } else if (isNaN(o1) && !isNaN(o2)) {
        if (key && typeof o1 === 'object') {
          v2 = o2;
        } else {
          return 1;
        }
      }

      /*
       * 
       *  key
       *  value  value 
       */

      // value 
      var isNum = [!isNaN(v1), !isNaN(v2)];

      // 
      if (isNum[0] && isNum[1]) {
        if(v1 && (!v2 && v2 !== 0)) { //  vs 
          return 1;
        } else if((!v1 && v1 !== 0) && v2) { //  vs 
          return -1;
        } else { //  vs 
          return v1 - v2;
        }
      }

      /**
       * 
       */

      // 
      if (!isNum[0] && !isNum[1]) {
        // 
        if (v1 > v2) {
          return 1;
        } else if (v1 < v2) {
          return -1;
        } else {
          return 0;
        }
      }

      // 
      if (isNum[0] || !isNum[1]) { //  vs 
        return -1;
      } else if(!isNum[0] || isNum[1]) { //  vs 
        return 1;
      }

    });

